import React from "react";
import s from "./Order.module.scss";
import {observer} from "mobx-react-lite";

interface IProps {
  positions: any[];
}

const OrderPositions = observer((props: IProps) => {
  const returnPositions = () => {
    return props.positions.map((el, key) => {
      return (
        <div className={s.posBlock} key={key}>
          <img src={el.iProduct.preview} alt="" />
          <div className={s.legend}>
          <div>Количество: {el._count}</div>
          <div>Цена: {el._sum} грн</div>
          </div>
        </div>
      );
    });
  };

  const returnSum = () => {
    return (
      <div className={s.orderSum}>
        Окончательная сумма: {props.positions.reduce((a, b) => {
          return a + b._sum;
        }, 0)} грн
      </div>
    );
  };

  return (
    <div className={s.orderPositions}>
      {returnPositions()}
      {returnSum()}
    </div>
  );
});

export default OrderPositions;
